import React, {useEffect} from "react";
import style from './searchForm.module.scss';
import {Field, Form, Formik, FormikHelpers, FormikProps, useFormik} from "formik";
import SearchIcon from '@mui/icons-material/Search';
import {IconButton} from "@mui/material";
import ClearIcon from '@mui/icons-material/Clear';
import {useLocation, useNavigate} from "react-router-dom";
import {useDispatch} from "react-redux";
import {bricksAC} from "../../../../store/reducers/bricks.reducer";

interface IValues {
    query: string
}

export const SearchForm = () => {
    const initialValues: IValues = {
        query: ''
    };

    let navigate = useNavigate();

    const dispatch = useDispatch();

    const onSubmitHandler = (
        values: IValues,
        formikHelpers: FormikHelpers<IValues>
    ) => {
        formikHelpers.setSubmitting(false);
        console.log(values);

        dispatch(bricksAC.setFilterParams({
            query: values.query,
            marks: [],
            fontKind: 'null',
            border: 'null',
            defect: 'null',
        }));
    };

    const formik = useFormik({
        initialValues,
        onSubmit: onSubmitHandler
    });

    const location = useLocation();

    // useEffect(() => {
    //     if (location.pathname !== "/products/search") {
    //         formik.resetForm()
    //     }
    // }, [location.pathname])

    return (
        <div className={style.searchForm}>
            <form onSubmit={formik.handleSubmit}>
                <IconButton onClick={() => formik.resetForm()}>
                    <ClearIcon/>
                </IconButton>
                <input type="text"
                       className={style.field}
                       autoComplete="off"
                       {...formik.getFieldProps("query")}
                />
                <IconButton type="submit">
                    <SearchIcon/>
                </IconButton>
            </form>
        </div>
    )
}