import {IChronoItem} from "../../../store/reducers/chrono.reducer";
import React, {FC, useState} from "react";
import clsx from "clsx";
import style from "./ChronoItem.module.scss";
import {Collapse, Skeleton} from "@mui/material";
import Zoom from "@mui/material/Zoom";
import Grow from "@mui/material/Grow";

interface IChronoItemComponent {
    item: IChronoItem
    selected: boolean
    onClickHandler: () => void
}

export const ChronoItem: FC<IChronoItemComponent> = ({
                                                         item,
                                                         selected,
                                                         onClickHandler
                                                     }) => {
    const [loading, setLoading] = useState(true);
    const onLoadHandler = () => setLoading(false);

    return (
        <div
            className={clsx({
                [style.item]: true,
                [style.item_selected]: selected,
            })}
            onClick={onClickHandler}
        >
            <div className={style.date}>
                {item.date}
            </div>

            <div className={style.imgWrapper}>
                <img src={item.src}
                     alt=""
                     className={style.img}
                     onLoad={onLoadHandler}
                />
                {
                    loading && item.src && (
                        <Skeleton variant="rectangular"
                                  sx={{ bgcolor: 'grey.500' }}
                                  className={style.skeleton}
                        />
                    )
                }
            </div>



            <div className={style.text}>
                <p>{item.text}</p>
                {
                    item.fullText &&
                    <Collapse in={selected} timeout={300}>
                        <p>{item.fullText}</p>
                    </Collapse>
                }

            </div>
        </div>
    )
}