import React, {FC, useState} from 'react';
import style from "./ChronoEditItem.module.scss";
import {deleteChronoItem, IChronoItem, updateChronoItem} from "../../../../store/reducers/chrono.reducer";
import {Button} from "@mui/material";
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import ClearIcon from '@mui/icons-material/Clear';
import EditIcon from '@mui/icons-material/Edit';
import {useDispatch} from "react-redux";
import {ChronoEditForm, ChronoItemUpdateType} from "../ChronoEditForm/ChronoEditForm";
import clsx from "clsx";

interface ChronoEditItem {
    chronoItem: IChronoItem
}

export const ChronoEditItem: FC<ChronoEditItem> = ({chronoItem}) => {
    const [showForm, setShowForm] = useState(false);
    const {id, ...initialValues} = chronoItem;

    const onEditHandler = () => setShowForm(!showForm);

    const dispatch = useDispatch();

    const onDeleteHandler = () => {
        dispatch(deleteChronoItem(chronoItem.id));
    };

    return (
        <div className={style.chronoEditItem}>
            <div className={style.header}>
                <div className={style.text}>
                    <p>
                        <span>{chronoItem.date}</span> - <span>{chronoItem.text}</span>
                    </p>
                </div>

                <Button variant="outlined"
                        size="small"
                        color={showForm ? "error" : "primary"}
                        onClick={onEditHandler}
                        startIcon={showForm ? <ClearIcon/> : <EditIcon/>}
                        className={clsx(style.btn, style.btnEdit)}
                >
                    {showForm ? '' : ''}
                </Button>

                <Button variant="outlined"
                        color="error"
                        size="small"
                        startIcon={<DeleteForeverIcon/>}
                        onClick={onDeleteHandler}
                        className={clsx(style.btn, style.btnDelete)}
                >
                    
                </Button>
            </div>

            {
                showForm && <ChronoEditForm formTitle=''
                                            initialValues={initialValues}
                                            onSubmitHandler={(values: ChronoItemUpdateType) => dispatch(updateChronoItem({id: chronoItem.id, ...values}))}
                                            onCloseHandler={() => setShowForm(false)}
                />
            }

        </div>
    );
};
