import React, {ChangeEvent, FC} from "react";
import style from "./personalData.module.scss"
import {Button, TextField} from "@mui/material";
import {useDispatch, useSelector} from "react-redux";
import {FormikHelpers, useFormik} from "formik";
import Typography from "@mui/material/Typography";
import {
    authAC, changeAvatar,
    changeNickName,
    selectLoading,
} from "../../../store/reducers/auth.reducer";
import grey from "@mui/material/colors/grey";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import styled from "@mui/material/styles/styled";
import {IAvatar} from "../../../types/types";
import {IUserInfo} from "../../../api/auth.api";

const Input = styled('input')({
    display: 'none',
});

export interface IValues {
    nickName: string
}

export interface IPersonalData {
    userInfo: IUserInfo | null
}

export const PersonalData: FC<IPersonalData> = ({userInfo}) => {
    const isLoading = useSelector(selectLoading);
    const dispatch = useDispatch();

    const initialValues: IValues = {
        nickName: userInfo ? userInfo.nickName : '',
    };

    const onSubmit = async ({nickName}: IValues, formikHelpers: FormikHelpers<IValues>) => {
        try {
            dispatch(changeNickName(nickName));
        } catch (e: any) {
            console.error(e.message);
            console.error(e.stack);
        } finally {
            dispatch(authAC.setLoading(false));
            formikHelpers.setSubmitting(false);
        }
    };
    const formik = useFormik<IValues>({
        initialValues,
        onSubmit,
        enableReinitialize: true,
    });

    const onChangeAvatarHandler = (e: ChangeEvent<HTMLInputElement>) => {
        console.log(e.target.value)
        console.log(e.target.files)

        if (e.target.files) {
            dispatch(changeAvatar(e.target.files[0]));
            e.target.value = "";
        }
    }

    return (
        <div className={style.personalData}>

            <div className={style.avatarBlock}>
                <div className={style.avatarWrapper}>
                    {
                        isLoading
                            ? (
                                <AccountCircleIcon sx={{
                                    fontSize: "170px",
                                    color: grey[500]
                                }}/>
                            )
                            : (userInfo && userInfo.avatar)
                            ? (
                                <img src={(userInfo.avatar as IAvatar).full} alt=""/>
                            )
                            : (
                                <AccountCircleIcon sx={{
                                    fontSize: "170px",
                                    color: grey[500]
                                }}/>
                            )
                    }
                </div>

                <label htmlFor="contained-button-file">
                    <Input accept="image/*"
                           id="contained-button-file"
                           type="file"
                           onChange={onChangeAvatarHandler}
                    />
                    <Button variant="contained"
                            component="span"
                            sx={{marginTop: "10px"}}>
                         
                    </Button>
                </label>

            </div>

            <div className={style.nicknameBlock}>
                <Typography></Typography>
                <form onSubmit={formik.handleSubmit}
                      className={style.form}
                >
                    <TextField type="text"
                               size="small"
                               fullWidth
                               variant="outlined"
                               id="nickName"
                               {...formik.getFieldProps("nickName")}
                               className={style.field}
                    />
                    <Button type='submit'
                            color="primary"
                            fullWidth
                            variant="contained"
                            className={style.field}
                            style={{marginTop: "10px"}}
                            disabled={formik.isSubmitting || isLoading}
                    >
                        
                    </Button>
                </form>
            </div>

        </div>
    )
}