import React, {useEffect, useState} from 'react';
import style from "./ChronoEdit.module.scss";
import {useDispatch, useSelector} from "react-redux";
import {
    addChronoItem,
    getChronoItems,
    selectChronoItems,
} from "../../../store/reducers/chrono.reducer";
import {ChronoEditItem} from "./ChronoEditItem/ChronoEditItem";
import clsx from "clsx";
import {Button} from "@mui/material";
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import {ChronoEditForm, ChronoItemUpdateType} from "./ChronoEditForm/ChronoEditForm";

export const ChronoEdit = () => {
    const [showForm, setShowForm] = useState(false);
    const chronoItems = useSelector(selectChronoItems);

    const chronoItemsSort = [...chronoItems].sort((a, b) => {
        if (a.date > b.date) return 1
        if (a.date < b.date) return -1
        return 0
    });

    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(getChronoItems());
    }, []);

    return (
        <div className={style.chronoEdit}>
            <Button variant="contained"
                    color="success"
                    size="small"
                    startIcon={<AddCircleOutlineIcon/>}
                    onClick={() => setShowForm(true)}
                    className={clsx(style.btnAdd, style.btnDelete)}
            >
                
            </Button>
            <div style={{marginTop: 10}}>
                {
                    showForm && <ChronoEditForm formTitle=''
                                                initialValues={{
                                                    date: '',
                                                    text: '',
                                                    fullText: '',
                                                    src: '',
                                                }}
                                                onSubmitHandler={(values: ChronoItemUpdateType) => dispatch(addChronoItem(values))}
                                                onCloseHandler={() => setShowForm(false)}
                    />
                }
            </div>
            <div>
                {
                    chronoItemsSort.map(chronoItem => (
                        <ChronoEditItem key={chronoItem.id} chronoItem={chronoItem}/>
                    ))
                }
            </div>


        </div>
    );
};
