import React, {useEffect, useState} from "react";
import style from "./chrono.module.scss"
import {useDispatch, useSelector} from "react-redux";
import {getChronoItems, selectChronoItems} from "../../store/reducers/chrono.reducer";
import {ChronoItem} from "./ChronoItem/ChronoItem";

export const Chrono = () => {
    const [currentIndex, setCurrentIndex] = useState(0);
    const chronoItems = useSelector(selectChronoItems);

    const chronoItemsSort = [...chronoItems].sort((a, b) => {
        if (a.date > b.date) return 1
        if (a.date < b.date) return -1
        return 0
    });

    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(getChronoItems());
    }, []);

    return (
        <section className={style.chrono}>
            <div className={style.inner}>
                <h1 className={style.title}></h1>
                <div className={style.items}>
                    {
                        chronoItemsSort.map((item, index) => (
                            <ChronoItem key={index}
                                        item={item}
                                        selected={index === currentIndex}
                                        onClickHandler={() => setCurrentIndex(index)}
                            />
                        ))
                    }
                </div>
            </div>
        </section>
    )
}