import {instance, instanceWithAccessToken, IResponse} from "./api";
import {IAvatar} from "../types/types";

export interface LoginDataType {
    accessToken: string
    // login: string
    // userId: string
    // email: string
    // role: 'user' | 'admin'
}

export type RoleType = "user" | "admin"

export interface IUserInfo {
    login: string
    email: string
    role: RoleType
    nickName: string
    avatar: IAvatar | null
}

export interface IRefreshResponse {
    accessToken: string
}

export const authAPI = {
    // 
    async registration(login: string, email: string, password: string) {
        const response = await instance.post<IResponse>('auth/register', {login, email, password});
        return response.data;
    },

    // 
    async login(email: string, password: string) {
        const response = await instance.post<IResponse<LoginDataType>>('auth/login', {email, password});
        return response.data;
    },

    //  
    async changePassword(oldPassword: string, newPassword: string) {
        const response = await instanceWithAccessToken.post<IResponse<LoginDataType>>(
            'auth/password',
            {oldPassword, newPassword}
        );
        return response.data;
    },

    //  access token
    async refresh() {
        const response = await instance.get<IResponse<IRefreshResponse>>('auth/refresh');
        return response.data;
    },

    // 
    async logout() {
        const response = await instance.post<IResponse>('auth/login');
        return response.data;
    },

    //   
    async getUserInfo() {
        const response = await instanceWithAccessToken.get<IResponse<IUserInfo>>('auth/info');
        return response.data;
    },

    //  nickName
    async changeNickName(nickName: string) {
        const response = await instanceWithAccessToken.put<IResponse<IResponse>>('auth/nickname',{nickName});
        return response.data;
    },

    //  avatar
    async changeAvatar(file: File) {
        const formData = new FormData();
        formData.append('file', file);
        const response = await instanceWithAccessToken.put<IResponse>('auth/avatar', formData);
        return response.data;
    }
};

