import React, {useState} from "react";
import style from "./EditPage.module.scss"
import Tab from "@mui/material/Tab";
import {Tabs} from "@mui/material";
import {ChronoEdit} from "./ChronoEdit/ChronoEdit";
import {MarkEdit} from "./MarkEdit/MarkEdit";
import {TabPanel} from "../common/TabPanel/TabPanel";


export const EditPage = () => {
    const [currentTab, setCurrentTab] = useState(0);
    const onChangeTab = (event: React.SyntheticEvent, newValue: number) => {
        setCurrentTab(newValue);
    };

    const tabs = [
        { label: '', content: <ChronoEdit />},
        { label: '', content: <MarkEdit />},
        { label: '', content: <ChronoEdit />},
    ];

    return (
        <section className={style.editPage}>
            <h1 className={style.title}> </h1>

            <Tabs value={currentTab}
                  onChange={onChangeTab}
                  aria-label="basic tabs example"
                  sx={{
                      marginTop: '10px',
                      '& .MuiTabs-indicator': {
                          height: "4px",
                      }
                  }}
            >
                {
                    tabs.map(({label}, i) => <Tab key={i} label={label}/>)
                }
            </Tabs>

            <div className={style.content}>
                {
                    tabs.map((el, i) => (
                        <TabPanel key={i} index={i} currentIndex={currentTab}>
                            {el.content}
                        </TabPanel>
                    ))
                }
            </div>
        </section>
    )
}