export const setAccessToken = (accessToken: string): void => {
    localStorage.setItem('oldGzhatskAccessToken', accessToken);
};

export const getAccessToken = (): null | string => {
    const accessToken = localStorage.getItem('oldGzhatskAccessToken');
    if (accessToken) {
        return accessToken
    } else {
        return null
    }
};

export const removeAccessToken = (): void => {
    localStorage.removeItem('oldGzhatskAccessToken');
};