import React, {FC} from 'react';
import style from './ChronoEditForm.module.scss';
import {IChronoItem} from "../../../../store/reducers/chrono.reducer";
import {Form, Formik, FormikErrors, FormikHelpers, FormikProps} from "formik";
import {FieldsetCustom} from "../../../common/FieldsetCustom/FieldsetCustom";
import {FieldText} from "../../../common/FieldText/FieldText";
import {Button} from "@mui/material";
import SendIcon from '@mui/icons-material/Send';
import ClearIcon from '@mui/icons-material/Clear';
import {FieldLoadImage} from "../../../common/FieldLoadImage/FieldLoadImage";

export type ChronoItemUpdateType = Omit<IChronoItem, "id">

interface IChronoEditForm {
    formTitle: string
    initialValues: ChronoItemUpdateType
    onSubmitHandler: (update: any) => void
    onCloseHandler: () => void
}

export const ChronoEditForm: FC<IChronoEditForm> = ({
                                                        formTitle,
                                                        initialValues,
                                                        onSubmitHandler,
                                                        onCloseHandler
                                                    }) => {


    const validate = (values: ChronoItemUpdateType): FormikErrors<ChronoItemUpdateType> => {
        const errors: FormikErrors<ChronoItemUpdateType> = {};
        if (!values.date) {
            errors.date = ' '
        }
        if (!values.text) {
            errors.text = ' '
        }
        return errors;
    };

    const onSubmit = async (values: ChronoItemUpdateType, formikHelpers: FormikHelpers<ChronoItemUpdateType>) => {
        try {
            onSubmitHandler(values);
            onCloseHandler();
        } catch (e: any) {
            //formikHelpers.setFieldError(e.response.data.error.field, e.response.data.error.value);
        } finally {
            formikHelpers.setSubmitting(false);
        }
    };

    return (
        <Formik initialValues={initialValues}
                onSubmit={onSubmit}
                validate={validate}
        >
            {
                (props: FormikProps<ChronoItemUpdateType>) => (
                    <FieldsetCustom label={formTitle} className={style.chronoEditForm}>
                        <Form className={style.form}>
                            <FieldText name="date"
                                       label=""
                                       size="small"
                                       placeholder=" "
                                       className={style.field}
                            />

                            <FieldText name="text"
                                       label=""
                                       size="small"
                                       placeholder=" "
                                       className={style.field}
                            />

                            <FieldText name="fullText"
                                       label=""
                                       size="small"
                                       placeholder=" "
                                       className={style.field}
                            />

                            <FieldLoadImage name="src" label="" className={style.fieldLoadImage}/>

                            <div className={style.buttons}>
                                <Button type='submit'
                                        variant="outlined"
                                        size="small"
                                        style={{textTransform: 'none', alignSelf: 'flex-start'}}
                                        startIcon={<SendIcon/>}
                                        disabled={props.isSubmitting}
                                >
                                    
                                </Button>
                                <Button variant="outlined"
                                        size="small"
                                        color="error"
                                        style={{marginLeft: "10px", textTransform: 'none'}}
                                        onClick={onCloseHandler}
                                        startIcon={<ClearIcon/>}
                                >
                                    
                                </Button>
                            </div>

                        </Form>
                    </FieldsetCustom>
                )
            }
        </Formik>
    );
};
