import React, {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {FormControl, Select, SelectChangeEvent, Theme} from "@mui/material";
import OutlinedInput from "@mui/material/OutlinedInput";
import Chip from "@mui/material/Chip";
import MenuItem from "@mui/material/MenuItem";
import useTheme from "@mui/material/styles/useTheme";
import Checkbox from "@mui/material/Checkbox";
import ListItemText from "@mui/material/ListItemText";
import style from './MarkFilter.module.scss';
import {ResetButton} from "../../../../common/ResetButton/ResetButton";
import {bricksAC, getAllMarks, selectFilterParams, selectMarks} from "../../../../../store/reducers/bricks.reducer";
import {IFilterParams} from "../../../../../types/types";

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        },
    },
};

function getStyles(name: string, personName: readonly string[], theme: Theme) {
    return {
        fontWeight:
            personName.indexOf(name) === -1
                ? theme.typography.fontWeightRegular
                : theme.typography.fontWeightMedium,
    };
}

export const MarkFilter = () => {
    const marksList = useSelector(selectMarks).map(el => el.name) ;
    const filterParams: IFilterParams = useSelector(selectFilterParams);
    const marksGlobal = filterParams.marks;

    const [marksLocal, setMarksLocal] = useState(filterParams.marks);

    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(getAllMarks());
    }, []);

    useEffect(() => {
        dispatch(bricksAC.setFilterParams({
            ...filterParams,
            marks: marksLocal,
        }));
    }, [marksLocal])

    const onChangeHandler = (event: SelectChangeEvent<typeof marksGlobal>) => {
        const value = event.target.value;
        console.log(value)
        setMarksLocal(typeof value === 'string' ? value.split(',') : value);
    };

    const onResetHandler = () => setMarksLocal([]);

    const theme = useTheme();

    return (
        <div className={style.MarkFilter}>
            <div className={style.header}>
                <p className={style.title}></p>
                <ResetButton onResetHandler={onResetHandler}/>
            </div>

            <div>
                {
                    marksLocal &&
                    <FormControl sx={{width: '100%'}}>
                        <div className={style.chips}>
                            {
                                marksGlobal.map(mark => (
                                        <Chip key={mark}
                                              label={mark}
                                              onDelete={() => setMarksLocal([...marksLocal].filter(currentMark => currentMark !== mark))}
                                              sx={{
                                                  background: '#FFF',
                                                  marginBottom: '5px',
                                                  '& .MuiChip-label': {
                                                      padding: '0 8px',
                                                  }
                                              }}
                                        />
                                    )
                                )
                            }
                        </div>
                        <Select
                            labelId="Mark"
                            id="Mark-multiple-chip"
                            multiple
                            size='small'
                            placeholder="Select brand"
                            value={marksGlobal}
                            onChange={onChangeHandler}
                            input={
                                <OutlinedInput id="select-multiple-chip"
                                               sx={{
                                                   margin: '5px 0 10px',
                                                   background: '#FFF',
                                                   '& .MuiOutlinedInput-notchedOutline': {
                                                       border: '2px solid rgba(255,255,255,0.8)',
                                                       transition: '0.3s',
                                                   },
                                                   '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                                                       border: '2px solid orange',
                                                   },
                                                   '&.Mui-focused:hover .MuiOutlinedInput-notchedOutline': {
                                                       border: '2px solid orange',
                                                   },
                                                   '&:hover .MuiOutlinedInput-notchedOutline': {
                                                       border: '2px solid orange',
                                                   },
                                               }}
                                />
                            }
                            renderValue={
                                (selected) => " "
                            }
                            MenuProps={MenuProps}
                        >
                            {marksList.map((mark) => (
                                <MenuItem
                                    key={mark}
                                    value={mark}
                                    //style={getStyles(brand, brandsGlobal, theme)}
                                >
                                    <Checkbox checked={marksGlobal.indexOf(mark) > -1}/>
                                    <ListItemText primary={mark}/>
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                }
            </div>
        </div>
    )
};