import React, {useEffect, useState} from "react";
import {Navigate} from "react-router-dom";
import style from "./accountSettingsPage.module.scss"
import {PersonalData} from "./PersonalData/PersonalData";
import {Security} from "./Security/Security";
import Tab from "@mui/material/Tab";
import {Tabs} from "@mui/material";
import grey from "@mui/material/colors/grey";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import Typography from "@mui/material/Typography";
import {getAccessToken} from "../../localStorage/localStorage";
import {a11yProps} from "../../helpers/helpers";
import {TabPanel} from "../common/TabPanel/TabPanel";
import {useDispatch, useSelector} from "react-redux";
import {getUserInfo, selectUserInfo} from "../../store/reducers/auth.reducer";

export const AccountSettingsPage = () => {
    const accessToken = getAccessToken();

    const userInfo = useSelector(selectUserInfo);

    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(getUserInfo());
    }, [])


    const tabs = [
        { label: " ", content: <PersonalData userInfo={userInfo}/>},
        { label: "", content: <Security/>},
    ];
    const [currentTabIndex, setCurrentTabIndex] = useState(0);
    const onChangeHandler = (event: React.SyntheticEvent, tabIndex: number) => {
        setCurrentTabIndex(tabIndex);
    };

    if (!accessToken) {
        return <Navigate to="/login"/>
    }
    return (
        <section className={style.accountSettingsPage}>
            <div className={style.leftBlock}>

                <div className={style.userInfo}>
                    <AccountCircleIcon sx={{fontSize: 80, color: grey[500]}}/>
                    {userInfo && <p className={style.login}>{userInfo?.login}</p>}
                    {userInfo && <p className={style.email}>{userInfo?.email}</p>}
                </div>

                <Tabs value={currentTabIndex}
                      onChange={onChangeHandler}
                      orientation="vertical"
                      aria-label="basic tabs example"
                      className={style.tabs}
                >
                    {
                        tabs.map(({label}, index) => (
                            <Tab key={index}
                                 label={label}
                                 {...a11yProps(index)}
                                 sx={{
                                     textTransform: "none"
                                 }}
                            />
                        ))
                    }
                </Tabs>
            </div>

            <div className={style.rightBlock}>
                <Typography variant="h5" component="h2" style={{marginBottom: "20px"}}>
                    {tabs[currentTabIndex].label}
                </Typography>
                {
                    tabs.map((el, i) => (
                        <TabPanel key={i}
                                  index={i}
                                  currentIndex={currentTabIndex}
                        >
                            {el.content}
                        </TabPanel>
                    ))
                }
            </div>
        </section>
    )
}