import {instance, instanceWithAccessToken, IResponse} from "./api";
import {IChronoItem} from "../store/reducers/chrono.reducer";

export const chronoAPI = {
    //============ GET ALL ===========//
    getItems: async () => {
        const response = await instance.get<IResponse<IChronoItem[]>>('chrono')
        return response.data;
    },

    //============ UPDATE ===========//
    update: async (id: string, dto: Omit<IChronoItem, "id">) => {
        const keys = Object.keys(dto);
        const formData = new FormData();

        keys.forEach(key => (
            // @ts-ignore
            formData.append(`${key}`, dto[key])
        ));

        const response = await instance.put<IResponse>(`chrono/${id}`, formData);
        return response.data;
    },

    //============ ADD ===========//
    add: async (dto: Omit<IChronoItem, "id">) => {
        const keys = Object.keys(dto);
        const formData = new FormData();

        keys.forEach(key => (
            // @ts-ignore
            formData.append(`${key}`, dto[key])
        ));

        const response = await instance.post<IResponse>('chrono', formData);
        return response.data;
    },

    //============ DELETE ===========//
    delete: async (id: string) => {
        const response = await instance.delete<IResponse>(`chrono/${id}`);
        return response.data;
    }

}