import {instance, IResponse} from "./api";
import {IBrick, IFilterParams, ILocation, ILocationFull, IMarkFull, IMarkItem} from "../types/types";

interface IGetBricks {
    bricks: IBrick[]
}

export const bricksAPI = {
    getBricksLocationsList: async () => {
        const response = await instance.get<IResponse<ILocation[]>>('bricks/location')
        return response.data;
    },
    getBricksLocationById: async (id: string) => {
        const response = await instance.get<IResponse<ILocationFull>>(`bricks/location/${id}`)
        return response.data;
    },
    getAllMarks: async () => {
        const response = await instance.get<IResponse<IMarkItem[]>>(`bricks/mark`);
        return response.data;
    },
    getMarkById: async (id: string) => {
        const response = await instance.get<IResponse<IMarkFull>>(`bricks/mark/${id}`);
        return response.data;
    },
    getBricks: async (filterParams: IFilterParams) => {
        const {query, marks, fontKind, border, defect} = filterParams;

        let url = 'bricks/brick?';

        let urlQuery = ''

        if (marks.length !== 0) {
            urlQuery = `${urlQuery}&marks=${filterParams.marks.join(',')}`;
        }

        Object.keys(filterParams).forEach(key => {
            // @ts-ignore
            if (filterParams[key] && key !== 'marks') {
                // @ts-ignore
                urlQuery = `${urlQuery}&${key}=${filterParams[key]}`;
            }
        });
        url = urlQuery ? url + urlQuery.slice(1) : url;


        const response = await instance.get<IResponse<IGetBricks>>(url);
        return response.data;
    }
};