import {BaseThunkType, GetActionsType, StateType} from "../store";
import {IBrick, IFilterParams, ILocation, ILocationFull, IMarkFull, IMarkItem} from "../../types/types";
import {bricksAPI} from "../../api/bricks.api";
import {bricksAC} from "./bricks.reducer";
import {chronoAPI} from "../../api/chrono.api";
import {authAC} from "./auth.reducer";
import {ChronoItemUpdateType} from "../../components/A5_EditPage/ChronoEdit/ChronoEditForm/ChronoEditForm";

//================ TYPE ==================
export type InitialStateType = typeof initialState;
export type ChronoActionsType = GetActionsType<typeof chronoAC>
type ThunkType = BaseThunkType<ChronoActionsType>

export interface IChronoItem {
    id: string
    date: string
    text: string
    fullText: string
    src: string
}

//================ INITIAL ==================
const initialState = {
    loading: false,
    chronoItems: [] as IChronoItem[],
};

//================ REDUCER ===================
export const chronoReducer = (state = initialState, action: ChronoActionsType): InitialStateType => {
    switch (action.type) {
        case 'GZHATSK/CHRONO/SET_LOADING': {
            return {...state, loading: action.loading}
        }
        case 'GZHATSK/CHRONO/SET_CHRONO_ITEMS': {
            return {...state, chronoItems: action.chronoItems}
        }
        default:
            return state;
    }
}

// ================ ACTION CREATORS =================
export const chronoAC = {
    setLoading: (loading: boolean) => ({type: 'GZHATSK/CHRONO/SET_LOADING', loading} as const),
    setChronoItems: (chronoItems: IChronoItem[]) => ({type: 'GZHATSK/CHRONO/SET_CHRONO_ITEMS', chronoItems} as const),
}

//================ THUNK CREATORS ==================

//================ GET ALL ==================//
export const getChronoItems = (): ThunkType => async (dispatch) => {
    try {
        dispatch(chronoAC.setLoading(true));
        const response = await chronoAPI.getItems();
        dispatch(chronoAC.setChronoItems(response.data));
    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(chronoAC.setLoading(false));
    }
}

//================ UPDATE ==================//
export const updateChronoItem = (dto: IChronoItem): ThunkType => async (dispatch) => {
        try {
            const {id, ...rest} = dto;
            dispatch(chronoAC.setLoading(true));
            await chronoAPI.update(dto.id, rest);
            await dispatch(getChronoItems());
        } catch (e: any) {
            console.error(e.message);
            console.error(e.stack);
        } finally {
            dispatch(chronoAC.setLoading(false));
        }
}

//================ ADD ==================//
export const addChronoItem = (dto: ChronoItemUpdateType): ThunkType => async (dispatch) => {
    try {
        dispatch(chronoAC.setLoading(true));
        await chronoAPI.add(dto);
        await dispatch(getChronoItems());
    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(chronoAC.setLoading(false));
    }
}

//================ DELETE ==================//
export const deleteChronoItem = (id: string): ThunkType => async (dispatch) => {
    try {
        dispatch(chronoAC.setLoading(true));
        await chronoAPI.delete(id);
        await dispatch(getChronoItems());
    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(chronoAC.setLoading(false));
    }
}


//============== SELECTORS =================
export const selectLoading = (state: StateType) => state.chrono.loading;
export const selectChronoItems = (state: StateType) => state.chrono.chronoItems;