import axios, {AxiosRequestConfig, AxiosResponse} from "axios";
import {getAccessToken, setAccessToken} from "../localStorage/localStorage";
import {authAPI} from "./auth.api";

export const instance = axios.create({
    baseURL: process.env.NODE_ENV === 'development'
        ? 'http://localhost:4444/gzhatsk/'
        : 'https://alexaltrex-common-api.herokuapp.com/gzhatsk/',
    withCredentials: true,
});

//  axios     end-point
export const instanceWithAccessToken = axios.create({
    baseURL: process.env.NODE_ENV === 'development'
        ? 'http://localhost:4444/gzhatsk/'
        : 'https://alexaltrex-common-api.herokuapp.com/gzhatsk/',
    withCredentials: true,
});

//        accessToken
instanceWithAccessToken.interceptors.request.use((config: AxiosRequestConfig) => {
    const accessToken = getAccessToken();
    if (accessToken && config.headers) {
        config.headers['x-access-token'] = accessToken;
    }
    return config;
});

//        
instanceWithAccessToken.interceptors.response.use(
    (response: AxiosResponse) => {
        return response;
    },
    async (error) => {
        const originalRequest = error.config; //      response.config
        if (error.response.data.message === 'Token Expired Error') {
            //     accessToken
            const getAccessTokenResponse = await authAPI.refresh();
            //  accessToken
            const accessToken = getAccessTokenResponse.data.accessToken;
            console.log(accessToken);
            //    localStorage
            setAccessToken(accessToken);
            //    (  accessToken)
            return instanceWithAccessToken(originalRequest);
        }
        return Promise.reject(error);
    }
);

export interface IResponse<D = {}> {
    status: 'ok' | 'error'
    data: D
    message: string
    error?: {
        field: string
        value: string
    }
}
