import {useDispatch, useSelector} from "react-redux";
import {bricksAC, selectFilterParams} from "../../../../../store/reducers/bricks.reducer";
import {FormControl, FormControlLabel} from "@mui/material";
import RadioGroup from "@mui/material/RadioGroup";
import Radio from "@mui/material/Radio";
import React, {FC} from "react";
import {ResetButton} from "../../../../common/ResetButton/ResetButton";
import style from "./BooleanRadioFilter.module.scss"
import {IFilterParams} from "../../../../../types/types";

interface IBooleanRadioFilter {
    title: string
    filterName: keyof IFilterParams
}

export const BooleanRadioFilter: FC<IBooleanRadioFilter> = ({
                                                                title,
                                                                filterName
}) => {
    const dispatch = useDispatch();
    const filterParams = useSelector(selectFilterParams);

    const sxRadio = {
        color: '#FFF',
        '&.Mui-checked': {
            color: 'orange',
        },
        '& .MuiSvgIcon-root': {
            fontSize: 16,
        },
    };

    const labels = [
        {value: 'null', label: ''},
        {value: 'true', label: ''},
        {value: 'false', label: ''},
    ];

    const onChangeHandler = (event: React.ChangeEvent<HTMLInputElement>, value: string) => {
        dispatch(bricksAC.setFilterParams({
            ...filterParams,
            [filterName]: value,
        }));
    };

    const onResetHandler = () => {
        dispatch(bricksAC.setFilterParams({
            ...filterParams,
            [filterName]: "null",
        }));
    };

    return (
        <div className={style.BooleanRadioFilter}>
            <div className={style.header}>
                <p className={style.title}>{title}</p>
                <ResetButton onResetHandler={onResetHandler}/>
            </div>


            <FormControl component="fieldset">
                <RadioGroup
                    aria-label={filterName}
                    value={filterParams[filterName]}
                    name="radio-buttons-group"
                    onChange={onChangeHandler}
                >
                    {
                        labels.map(el => (
                            <FormControlLabel key={el.label}
                                              value={el.value}
                                              control={<Radio sx={sxRadio}/>}
                                              label={el.label}
                                              sx={{color: '#FFF'}}
                            />
                        ))
                    }
                </RadioGroup>
            </FormControl>
        </div>
    )
};