import {BaseThunkType, GetActionsType, StateType} from "../store";
import {authAPI, IUserInfo} from "../../api/auth.api";
import {IAvatar} from "../../types/types";

//================ TYPE ==================
export type InitialStateType = typeof initialState;
export type AuthActionsType = GetActionsType<typeof authAC>
type ThunkType = BaseThunkType<AuthActionsType>

//================ INITIAL ==================
const initialState = {
    userInfo: null as null | IUserInfo,
    loading: false,
};

//================ REDUCER ===================
export const authReducer = (state = initialState, action: AuthActionsType): InitialStateType => {
    switch (action.type) {
        case 'GZHATSK/AUTH/SET_LOADING': {
            return {...state, loading: action.loading}
        }
        case 'GZHATSK/AUTH/SET_USER_INFO': {
            return {...state, userInfo: action.userInfo}
        }
        default:
            return state;
    }
};

// ================ ACTION CREATORS =================
export const authAC = {
    setLoading: (loading: boolean) => ({type: 'GZHATSK/AUTH/SET_LOADING', loading} as const),
    setUserInfo: (userInfo: null | IUserInfo) => ({type: 'GZHATSK/AUTH/SET_USER_INFO', userInfo} as const),
};

//================ THUNK CREATORS ==================

//================ GET USER INFO =================
export const getUserInfo = (): ThunkType => async (dispatch) => {
    try {
        dispatch(authAC.setLoading(true));
        const response = await authAPI.getUserInfo();
        dispatch(authAC.setUserInfo(response.data));

    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(authAC.setLoading(false));
    }
}

//===============   ================
export const changeNickName = (nickName: string): ThunkType => async (dispatch) => {
    try {
        dispatch(authAC.setLoading(true));
        await authAPI.changeNickName(nickName);
        await dispatch(getUserInfo());
    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(authAC.setLoading(false));
    }
};

//===============   ================
export const changeAvatar = (file: File): ThunkType => async (dispatch) => {
    try {
        dispatch(authAC.setLoading(true));
        await authAPI.changeAvatar(file);
        await dispatch(getUserInfo());
    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(authAC.setLoading(false));
    }
}

//=============== LOGOUT ================
export const logout = (): ThunkType => async (dispatch) => {
    try {
        dispatch(authAC.setLoading(true));
        await authAPI.logout();
        dispatch(authAC.setUserInfo(null));
    } catch (e: any) {
        console.error(e.message);
        console.error(e.stack);
    } finally {
        dispatch(authAC.setLoading(false));
    }
}

//============== SELECTORS =================
export const selectLoading = (state: StateType) => state.auth.loading;
export const selectUserInfo = (state: StateType) => state.auth.userInfo;