import React, {useEffect} from "react";
import style from "./BySearch.module.scss";
import {SearchForm} from "./SearchForm/SearchForm";
import {BooleanRadioFilter} from "./LeftFilters/BooleanRadioFilter/BooleanRadioFilter";
import {MarkFilter} from "./LeftFilters/MarkFilter/MarkFilter";
import {useSearchParams} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {bricksAC, getBricks, selectFilterParams} from "../../../store/reducers/bricks.reducer";
import {serializeSearchParams} from "../../../helpers/helpers";

export const BySearch = () => {
    let [searchParams, setSearchParams] = useSearchParams();
    const dispatch = useDispatch();
    const filterParams = useSelector(selectFilterParams);

    // URL => STATE
    useEffect(() => {
        const nextFilterParams = {...filterParams};
        const marks = searchParams.get('marks');
        const fontKind = searchParams.get('fontKind');
        const border = searchParams.get('border');
        const defect = searchParams.get('defect');

        console.log(marks);
        console.log(fontKind);
        console.log(border);
        console.log(defect);

        if (marks) {
            nextFilterParams.marks = marks.split(',');
        }

        if (fontKind && fontKind !== "null") {
            nextFilterParams.fontKind = fontKind;
        }

        if (border && border !== "null") {
            nextFilterParams.border = border;
        }

        if (defect && defect !== "null") {
            nextFilterParams.defect = defect;
        }

        dispatch(bricksAC.setFilterParams(nextFilterParams));

    }, []);

    // STATE => URL
    useEffect(() => {
        const nextSearchParams = {...serializeSearchParams(searchParams)};

        if (filterParams.marks.length !== 0) {
            nextSearchParams.marks = filterParams.marks.join(',');
        }

        if (filterParams.fontKind === "null") {
            delete nextSearchParams.fontKind;
        } else {
            nextSearchParams.fontKind = filterParams.fontKind;
        }

        if (filterParams.border === "null") {
            delete nextSearchParams.border;
        } else {
            nextSearchParams.border = filterParams.border;
        }

        if (filterParams.defect === "null") {
            delete nextSearchParams.defect;
        } else {
            nextSearchParams.defect = filterParams.defect;
        }

        setSearchParams(nextSearchParams);

        dispatch(getBricks(filterParams));

    }, [filterParams]);



    return (
        <div className={style.bySearch}>
            <div className={style.filters}>
                <div className={style.filter}>
                    <BooleanRadioFilter title="" filterName="border"/>
                </div>
                <div className={style.filter}>
                    <BooleanRadioFilter title="" filterName="defect"/>
                </div>
                <div className={style.filter}>
                    <MarkFilter/>
                </div>


            </div>
            <div className={style.content}>
                <SearchForm/>
            </div>

        </div>
    )
}